a{
    text-decoration: none;
    color: black;
}
.title_container{
    width: 1240px;
    height: 100px;
    display: flex;
    align-items: center;
    margin: 80px 0px 0px 250px;

}
.title_container h1{
    color: #004C59;
    font-size: 30px;    
}
.table-container {
    max-width: 1240px;
    margin: 10px 0px 0px 250px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 8px;

    /* font-family: -apple-system, BlinkMacSystemFont, Arial, sans-serif; */
}
th i {
    margin-left: 5px;
    color: #999;
    cursor: pointer;

}
table {
    width: 100%;
    border-collapse: collapse;
    background: white;
}

th {
    text-align: left;
    padding: 12px 16px;
    color: #666;
    border-bottom: 1px solid #eee;
    font-weight: 500;
}

td {
    padding: 12px 16px;
    border-bottom: 1px solid #eee;
}

tr:hover {
    background-color: #f8f9fa;
}

.status {
    display: inline-flex;
    align-items: center;
    gap: 6px;
}

.status-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

.status-active {
    background-color: #22c55e;
}

.status-blocked {
    background-color: #ef4444;
}

.status-locked {
    background-color: #64748b;
}

.action-buttons {
    display: flex;
    gap: 8px;
}

.btn {
    padding: 6px;
    border: 1px solid #e5e7eb;
    border-radius: 6px;
    background: white;
    cursor: pointer;
}

.btn:hover {
    background: #f8f9fa;
}

.pagination {
    display: flex;
    gap: 4px;
    margin-top: 16px;
    align-items: center;
}

.page-item {
    padding: 8px 12px;
    border: 1px solid #e5e7eb;
    border-radius: 6px;
    cursor: pointer;
}

.page-item.active {
    background: #2563eb;
    color: white;
    border-color: #2563eb;
}

.items-per-page {
    margin-left: 16px;
    padding: 8px;
    border: 1px solid #e5e7eb;
    border-radius: 6px;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
  
  .modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 12px;
    min-width: 300px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.2);
  }
  
  .modal-content h3 {
    margin-bottom: 15px;
  }
  
  .modal-content label {
    display: block;
    margin-bottom: 10px;
  }
  
  .modal-content select {
    width: 100%;
    padding: 8px;
    margin-top: 4px;
  }
  
  .modal-actions {
    text-align: right;
    margin-top: 20px;
  }
  
  .modal-actions button {
    padding: 6px 12px;
    margin-left: 8px;
  }